import React, { Component } from 'react'
import EmployeeService from '../services/EmployeeService';

class CreateEmployeeComponent extends Component {
    constructor(props) {
        super(props)

        this.state = {
            // step 2
            id:'',
            username: '',
            fullname: '',
            email: '',
            password: '',
            repassword:''
        }
        this.changeUserNameHandler = this.changeUserNameHandler.bind(this);
        this.changeFullNameHandler = this.changeFullNameHandler.bind(this);
        this.changeEmailHandler = this.changeEmailHandler.bind(this);
        this.changePasswordHandler = this.changePasswordHandler.bind(this);
        this.changeRepasswordHandler = this.changeRepasswordHandler.bind(this);
        this.saveOrUpdateEmployee = this.saveOrUpdateEmployee.bind(this);
    }

    // step 3
    componentDidMount(){

        // step 4
        if(this.state.id === '_add'){
            return
        }    
    }
    saveOrUpdateEmployee = (e) => {

        e.preventDefault();
        let pass= document.getElementById('password').value;
        let repass= document.getElementById('repassword').value;
        if(pass===repass)
        {
        let user = {fullname: this.state.fullname, username: this.state.username, email: this.state.email, password: this.state.password}
        console.log('user => ' + JSON.stringify(user));
        EmployeeService.createEmployee(user).then(res =>{
            let employee = res.data;    // employee variable has the data which is retured from controller
            // it will print the data from controller 
            var comp= JSON.stringify(employee);
         
            if(comp==='"success"')
            {
                window.location.replace("http://localhost:8080/login");
            }
           
            else
            {
                document.getElementById('resvalue').innerHTML=comp;
            }
                       });
                    }
                    else
                    {
                        document.getElementById('resvalue').innerHTML="Password And Re-enter Password Should match"
                    }
    }
    
        // step 5

 
    
    changeUserNameHandler= (event) => {
        this.setState({username: event.target.value});
    }

    changeFullNameHandler= (event) => {
        this.setState({fullname: event.target.value});
    }

    changePasswordHandler= (event) => {
        this.setState({password: event.target.value});
    }

    changeRepasswordHandler= (event) => {
        this.setState({repassword: event.target.value});
    }

    changeEmailHandler= (event) => {
        this.setState({email: event.target.value});
    }

    cancel(){
        this.props.history.push('/');
    }

    getTitle(){
        if(this.state.id === '_add'){
            return <h3 className="text-center">LOGIN</h3>
        }else{
            return <h3 className="text-center">REGISTRATION</h3>
        }
    }
    render() {
        return (
            <div>
                <br></br>
                   <div className = "container"  style={{ marginTop: `120px`, marginLeft:'190px'  }}>
                        <div className = "row">
                            <div className = "card col-md-6 offset-md-3 offset-md-3">
                                {
                                    this.getTitle()
                                }
                                <div className = "card-body">
                                    <form>
                                        <div className = "form-group">
                                            <label> User Name: </label>
                                            <input placeholder="User Name"  required  name="username" className="form-control" 
                                                value={this.state.userName} onChange={this.changeUserNameHandler}/>
                                        </div>
                                        <div className = "form-group">
                                            <label> Full Name: </label>
                                            <input placeholder="Full Name" required name="fullname" className="form-control" 
                                                value={this.state.fullname} onChange={this.changeFullNameHandler}/>
                                        </div>
                                        <div className = "form-group">
                                            <label> Email Id: </label>
                                            <input placeholder="Email Address"  required name="email" className="form-control" 
                                                value={this.state.email} onChange={this.changeEmailHandler}/>
                                        </div>

                                        <div className = "form-group">
                                            <label> Password: </label>
                                            <input placeholder="Password" type="password" required name="password" id="password" className="form-control" 
                                                value={this.state.password} onChange={this.changePasswordHandler}/>
                                        </div>

                                        <div className = "form-group">
                                            <label> Confirm Password: </label>
                                            <input placeholder="Re-Enter Password " type="password" required name="repassword" id="repassword" className="form-control" 
                                                value={this.state.repassword} onChange={this.changeRepasswordHandler}/>
                                        </div>
                                        <div className = "form-group">
                                           <h4 id="resvalue"></h4>
                                        </div>
                                        
                                        <br></br>
                                        <button className="btn btn-success" onClick={this.saveOrUpdateEmployee} style={{marginLeft: "10px",width:`80px`}}>SignUp</button>
                                        <button className="btn btn-danger" onClick={this.cancel.bind(this)} style={{marginLeft: "10px",width:`80px`}}>Login</button>
                                    </form>
                                </div>
                            </div>
                        </div>

                   </div>
            </div>
        )
    }
}

export default CreateEmployeeComponent
