import React, { Component } from 'react'
import EmployeeService from '../services/EmployeeService';
class CreateLogin extends Component {
    constructor(props) {
        super(props)

        this.state = {
            // step 2
            
            username: '',
            password: ''
        }
        this.changeUserNameHandler = this.changeUserNameHandler.bind(this);
        this.changePasswordHandler = this.changePasswordHandler.bind(this);
        this.saveOrUpdateEmployee = this.saveOrUpdateEmployee.bind(this);
        this.addSignup= this.addSignup.bind(this);
    }

    // step 3
    componentDidMount(){

        // step 4
        if(this.state.id === '_add'){
            return
        }
    }
    saveOrUpdateEmployee = (e) => {
        e.preventDefault();
        let user = { username: this.state.username,  password: this.state.password}
        console.log('user => ' + JSON.stringify(user));
        EmployeeService.loginUser(user).then(res =>{
            let employee = res.data;    // employee variable has the data which is retured from controller
            // it will print the data from controller 
            var comp= JSON.stringify(employee);
         
            if(comp==='"Admin"')
            {
                window.location.replace("http://localhost:8080/adminPage");
            }
            else if(comp==='"registered"')
            {
                window.location.replace("http://localhost:8080/registeredUser");
            }
            else
            {
               document.getElementById('resvalue').innerHTML= "Invalid UserName Or Password!!";
            }
                       });
    }
    
    changeUserNameHandler= (event) => {
        this.setState({username: event.target.value});
    }

    changePasswordHandler= (event) => {
        this.setState({password: event.target.value});
    }

/*
*/

    addSignup(){
        this.props.history.push('/save');
    }

    cancel=(event)=>{
        
        event.preventDefault();
        window.location.replace("http://localhost:8080");
        //this.props.history.push('/');
    }

    getTitle(){
        if(this.state.id === '_add'){
            return <h3 className="text-center">Add Employee</h3>
        }else{
            return <h3 className="text-center">LOGIN FORM</h3>
        }
    }
    render() {
        return (
            <div>
                <br></br>
                   <div className = "container" style={{ marginTop: `150px`, marginLeft:'190px'  }}>
                        <div className = "row">
                            <div className = "card col-md-6 offset-md-3 offset-md-3">
                                {
                                    this.getTitle()
                                }
                                
                                <div className = "card-body">
                                    <form>
                                        <div className = "form-group">
                                            <label> User Name: </label>
                                            <input placeholder="UserName" name="username" className="form-control" 
                                                value={this.state.username} onChange={this.changeUserNameHandler}/>
                                        </div>
                                        <div className = "form-group">
                                            <label> Password: </label>
                                            <input type="password" placeholder="Enter password" name="password" className="form-control" 
                                                value={this.state.password} onChange={this.changePasswordHandler}/>
                                        </div>
                                        <div className = "form-group">
                                        <h4 id="resvalue"></h4>
                                        </div>
                                        <br></br>

                                        <button className="btn btn-success" onClick={this.saveOrUpdateEmployee} style={{marginLeft: "10px",width:`80px`}}>Login</button>
                                        <button className="btn btn-primary" onClick={this.addSignup} style={{marginLeft: "10px",width:`80px`}}>SignUp</button>
                                        <button className="btn btn-danger" onClick={this.cancel} style={{marginLeft: "10px",width:`80px`}}>Back</button>
                                    </form>
                                </div>
                            </div>
                        </div>

                   </div>
            </div>
        )
    }
}

export default CreateLogin
